@import url(https://fonts.googleapis.com/css?family=Permanent+Marker);

h1, h2, h3, h4, h5, h6 {
  color: $color-headers;
  display: inline-block;
  background-color: #fff;
  padding: .5*$gutter 2*$gutter; }


a {
  text-decoration: none;
  color: $color-link;
  text-decoration: none; }
a:link {
  text-decoration: none; }

a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: underline; }

a:active {
  text-decoration: underline; }



